<template>
    <yd-layout>
        <div class="progressInquiry">
            <div class="header">
                <div class="h_left">申请时间</div>
                <div class="h_right" @click="open()">{{time}}</div>
                <yd-datetime class="dateColor" ref="datetime" v-model="time" slot="right" v-show="false"></yd-datetime>
            </div>
            <div class="progree">
                <div class="title">进度查询</div>
                <ul>
                    <li class="has_li"></li>
                    <li class="li_cricle">
                        <span class="has_cricle"></span>
                        <span class="has_cricle"></span>
                        <span class="has_cricle"></span>
                        <span class="has_cricle"></span>
                        <span class="has_cricle"></span>
                    </li>
                    <li class="has_li"></li>
                    <li class="li_cricle">
                        <span class="has_cricle"></span>
                        <span class="has_cricle"></span>
                        <span class="has_cricle"></span>
                        <span class="has_cricle"></span>
                        <span class="has_cricle"></span>
                    </li>
                    <li class="not_li"></li>
                    <li class="li_cricle">
                        <span class="has_cricle not_cricle"></span>
                        <span class="has_cricle not_cricle"></span>
                        <span class="has_cricle not_cricle"></span>
                        <span class="has_cricle not_cricle"></span>
                        <span class="has_cricle not_cricle"></span>
                    </li>
                    <li class="not_li"></li>
                </ul>
                <div class="proName">
                    <span>申请已受理</span>
                    <span class="proSpan2">资料处理中</span>
                    <span class="proSpan3">审核中</span>
                    <span class="proSpan4">审核完成</span>
                </div>
            </div>
            <div class="formDetail">
                <div class="detailHeader" @click="packUp($refs.detailBottom)">
                    <div class="title">申请表详情查看</div>
                    <div class="up" v-text="buttomName"></div>
                </div>
                <div class="detailBottom" ref="detailBottom">
                    <div class="incomeUse">
                        <div class="iuTitle">收入证明用途</div>
                        <div class="iuDetail">
                            1.用途（购房、买车、信用贷）；<br>2.月还款金额（总借贷150万，月还款1.5万）；<br>3.还款人员情况（如：夫妻二人，配偶方可开具：0.8万）
                        </div>
                    </div>
                    <div class="incomeUse">
                        <div class="iuTitle">收入证明抬头</div>
                        <div class="iuDetail">
                            招商银行福州路支行
                        </div>
                    </div>
                    <div class="incomeUse lastIU">
                        <div class="iuTitle">收入证明选项</div>
                        <div class="iuDetail">
                            税后工资
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </yd-layout>
</template>
<script>
import $ from '../../jquery-vendor';
export default {
    name: 'progressInquiry',
    data() {
        return {
            time: '',
            numTime: 0,
            buttomName: '收起'
        }
    },
    mounted() {
        let vm = this;
        vm.time = vm.formatDate(new Date(), 'yyyy-MM-dd hh:mm');
    },
    methods: {
        open() {
            this.$refs.datetime.open();
        },
        packUp(event) {
            let vm = this;
            vm.numTime++;
            if (vm.numTime % 2 == 1) {
                vm.buttomName = '展开';
            } else {
                vm.buttomName = '收起';
            }
            let className = event.className;
            $('.' + className).slideToggle();
        }
    }
}
</script>
<style lang="less" scoped>
@import 'proofofEmp.less';
</style>


